<template>
  <div class="containerWrap">
    <el-container class="container">
      <el-main class="rightMain">
        <el-container>
          <el-header class="dzy-main-header">
            <div class="btn-group-el">
              <el-button type="primary" icon="el-icon-plus" @click="add"
                >新增</el-button
              >
              <el-button type="success" icon="el-icon-view" @click="get"
                >查看</el-button
              >
              <el-button type="danger" icon="el-icon-delete" @click="del"
                >删除</el-button
              >
              <el-button type="warning" icon="el-icon-edit" @click="edit"
                >修改</el-button
              >
            </div>
            <div class="dzy-custom-search-box">
              <div>
                党员姓名 :
                <el-input
                  size="small"
                  placeholder="请输入姓名"
                  v-model="queryData.data.memberName"
                  clearable
                  class="dzy-custom-input"
                ></el-input>
              </div>
              <div>
                <el-button
                  size="small"
                  @click="searchHandleClick"
                  type="primary"
                  icon="el-icon-search"
                  >查询</el-button
                >
              </div>
            </div>
          </el-header>
          <el-main class="tableMain">
            <el-table
              ref="multipleTable"
              :data="tableData.records"
              style="width: 100%"
              :header-cell-style="{ backgroundColor: '#CF4B34', color: '#fff' }"
              :max-height="430"
              @select="selectChange"
              @row-click="rowClickBack"
              stripe
            >
              <el-table-column type="selection" width="55"></el-table-column>
              <el-table-column
                v-for="(item, index) in tableColumns"
                :key="index"
                :prop="item.prop"
                :width="item.width"
                :label="item.label"
              ></el-table-column>
            </el-table>
            <el-pagination
              background
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-size="pageSize"
              layout="total, prev, pager, next, jumper"
              :total="tableData.total"
            ></el-pagination>
          </el-main>
        </el-container>
      </el-main>
    </el-container>
    <el-dialog
      class="dialogForm"
      :title="dialogTitle"
      :visible.sync="dialogVisible"
      width="50%"
      :before-close="handleClose"
    >
      <el-form
        :model="formData"
        :rules="rules"
        ref="formData"
        label-width="100px"
        size="small"
      >
        <el-divider content-position="left">基本信息</el-divider>
        <el-row :gutter="24">
          <el-col :span="8">
            <el-form-item label="党员名称 :" prop="memberCardNumber">
              <el-input
                v-model="formData.memberCardNumber"
                disabled
                placeholder="党员名称"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="先锋类型 :" prop="memberName">
              <el-input
                v-model="formData.memberName"
                placeholder="先锋类型"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="24">
          <el-col :span="24">
            <el-form-item label="标题 :" prop="sexCode">
              <el-input
                v-model="formData.sexCode"
                placeholder="标题"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="24">
          <el-col :span="24">
            <el-form-item label="上传 :" prop="memberCardTypeCode">
              <el-input
                v-model="formData.memberCardTypeCode"
                placeholder="上传"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="24">
          <el-col :span="24">
            <el-form-item label="简介 :" prop="memberDesc">
              <quill-editor
                v-model="formData.content"
                ref="myQuillEditor"
                style="height: 300px"
              ></quill-editor>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose" v-if="showCommit">取 消</el-button>
        <el-button type="primary" @click="commit" v-if="showCommit"
          >提 交</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { quillEditor } from "vue-quill-editor";
import AppUpload from "@/components/AppAvatarUpload";
import AppTree from "@/components/common/AppTree";
import { getAllDeptTree } from "@/http/AppApi";
import {
  getPartyMembers,
  delPartyMember,
  getPartyMemberDet,
  addPartyMember,
  updatePartyMember,
} from "@/http/dangwuApi";
export default {
  data() {
    return {
      moveData: {},
      dialogMoveVisible: false,
      showCommit: true,
      dialogVisible: false,
      dialogTitle: "",
      pageSize: 10,
      currentPage: 1,
      queryData: {
        current: 1,
        size: 10,
        data: {},
      },
      formData: {},
      rules: {},
      tableColumns: [
        {
          prop: "memberName",
          label: "姓名",
          width: "150px",
        },
        {
          prop: "memeberStatus",
          label: "党员状态",
          width: "",
        },
        {
          prop: "cloudOrg.orgTelNumber",
          label: "党员编号",
          width: "200px",
        },
        {
          prop: "typeCode",
          label: "党员类型",
          width: "",
        },
        {
          prop: "memberPhoneNumber",
          label: "联系方式",
          width: "",
        },
        {
          prop: "cloudOrg.orgAddress",
          label: "隶属组织",
          width: "",
        },
      ],
      multipleSelection: null,
      tableData: [],
    };
  },
  mounted() {
    this.getTableData();
  },
  components: { AppTree, AppUpload, quillEditor },
  methods: {
    //查询按钮
    searchHandleClick() {
      this.getTableData();
    },
    //分页查询
    handleCurrentChange(val) {
      this.queryData.current = val;
      this.getTableData();
    },

    //获取表格数据
    getTableData() {
      getPartyMembers(this.queryData).then((resp) => {
        let data = resp.value;
        if (data && data.code == "0000") {
          this.tableData = data.data;
        } else {
          this.$message.error(data.message);
        }
      });
    },
    //新增
    add() {
      this.dialogVisible = true;
      this.dialogTitle = "新增党组织";
      this.showCommit = true;
    },
    //查看
    get() {
      if (!this.multipleSelection) {
        this.$message.warning("请选择要查看的行");
      } else {
        let params = { id: this.multipleSelection.id };
        getPartyMemberDet(params).then((resp) => {
          let data = resp.value;
          if (data && data.code == "0000") {
            this.formData = data.data;
            this.dialogTitle = "查看" + this.multipleSelection.memberName;
            this.showCommit = false;
            this.dialogVisible = true;
          } else {
            this.$message.error(data.messsage);
          }
        });
      }
    },
    //编辑
    edit() {
      if (!this.multipleSelection) {
        this.$message.warning("请选择要修改的行");
      } else {
        let params = { id: this.multipleSelection.id };
        getPartyMemberDet(params).then((resp) => {
          let data = resp.value;
          if (data && data.code == "0000") {
            this.formData = data.data;
            this.dialogTitle = "修改" + this.multipleSelection.memberName;
            this.showCommit = true;
            this.dialogVisible = true;
          } else {
            this.$message.error(data.messsage);
          }
        });
      }
    },
    //党组织关系转移
    move() {
      if (!this.multipleSelection) {
        this.$message.warning("请选择要修改的行");
      } else {
        this.dialogMoveVisible = true;
      }
    },
    //提交
    commit() {
      if (this.formData.id) {
        updatePartyMember(this.formData).then((resp) => {
          let data = resp.value;
          if (data && data.code == "0000") {
            this.$message.success("修改成功");
            this.handleClose();
          } else {
            this.$message.error(data.message);
          }
        });
      } else {
        addPartyMember(this.formData).then((resp) => {
          let data = resp.value;
          if (data && data.code == "0000") {
            this.$message.success("新增成功");
            this.handleClose();
          } else {
            this.$message.error(data.message);
          }
        });
      }
    },
    //删除
    del() {
      if (!this.multipleSelection) {
        this.$message.warning("请选择要删除的行");
      } else {
        this.$confirm("确定删除该组织吗?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        })
          .then(() => {
            delPartyMember().then((resp) => {
              let data = resp.value;
              if (data && data.code == "0000") {
                this.$message.success("删除成功!");
              } else {
                this.$message.error(data.message);
              }
            });
          })
          .catch(() => {
            this.$message.info("已取消删除!");
          });
      }
    },

    //点击表格行选中
    rowClickBack(row, column, event) {
      this.$refs.multipleTable.clearSelection();
      this.$refs.multipleTable.toggleRowSelection(row);
      this.multipleSelection = row;
    },
    //单选框变化
    selectChange(selection, row) {
      this.$refs.multipleTable.clearSelection();
      this.$refs.multipleTable.toggleRowSelection(row);
      this.multipleSelection = row;
    },
    //弹出框关闭之前
    handleClose() {
      this.formData = {};
      this.dialogVisible = false;
    },
  },
};
</script>
<style scoped>
.containerWrap {
  padding: 10px;
  height: 100%;
  box-sizing: border-box;
}
.container {
  height: 100%;
}
.dialogForm /deep/ .el-dialog .el-dialog__footer {
  margin-top: 50px;
}
</style>



